import React from 'react';
import ReactDOM from 'react-dom';
import { ChildClass } from './child';
import axios from 'axios';
import './index.css';

class ParentClass extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			astronauts: '',
			people: [{'name': '', 'craft': ''}] 
		};
		this.handleClick = this.handleClick.bind(this);
	}

	handleClick() {
		const url = "http://api.open-notify.org/astros.json";

		axios.get(url)
			 .then(response => {
				 this.setState({
					astronauts: response.data.number,
					people: response.data.people
				 });
			 });

		var btn = document.getElementById('count-button');
		btn.remove();
	}

	render() {
		return (
			<div>
				<ChildClass astros={this.state.astronauts} people={this.state.people} onClick={this.handleClick} />
			</div>
		)
	}
}

ReactDOM.render(<ParentClass />, document.getElementById('root'));


