import React from 'react';

export class ChildClass extends React.Component {
  
  render() {

    let descriptions = [
      " Brave Voyagers",
      " Space Cowboys",
      " Absolute Legends",
      " Human Life-Forms"
    ]

    let astronauts = ((this.props.astros > 0) ? 
    this.props.people.map((anObjectMapped, index) => {
      return <li id="name" key={index}>{anObjectMapped.name}</li>
    }) :  null )

    let description;
    if (this.props.astros > 0) {
      description = descriptions[Math.floor(Math.random()*(descriptions.length))]
    }

    return (
      <div className="wrapper">
        <div className="row">
		      <div className="title twelve columns">
            <h1>How many astronauts are in space?</h1>
		      </div>
        </div>
        <div className="row">
		      <div className="astros twelve columns">
		        <h1 id="count">{this.props.astros + (description ? description : '')}</h1>
              <div className="buttonDiv">
		            <button id="count-button" onClick={this.props.onClick}>
				          Click Me!
				        </button>
		          </div>
            </div>
              
            <div className="astros-names twelve columns">
              <ul className="names">
                {astronauts}
		          </ul>
            </div>
          </div>
        </div>
      );
    }
  }


